.home {
  position: relative;
  height: 100vh;  
  box-sizing: border-box;
  padding: 0;
  display: flex;
  justify-content: center;
  color: #F5F8FA ;
}

.home .content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.home .content h1 {
  font-weight: bold;
}

.home .content h1 span{
  color: #1DA1F2;
}


.home .content p {
  font-size: 32px;
  font-weight: 500;
}

.home .content p span{
  color: #1DA1F2;
  font-size: 38px;
}

.home .content button a {
  color: white;
}

.home .content button{
  font-size: 24px;
  font-weight: bold;
  background: #1DA1F2;
  color: #F5F8FA;
  padding: 8px 16px;
}
.home .content button:hover{
  opacity: 0.6;
}

@media screen and (max-width: 950px) {
  .home{
    height: 100vh;
    background: none;
  }
  .home .content h1{
    font-size: 23px;
  }
  .home .content p{
    font-size: 17px;
  }
}

